import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UPCOMINGCAMPAIGNSComponent } from './upcomingcampaigns/upcomingcampaigns.component';
import { LIVECAMPAIGNSComponent } from './livecampaigns/livecampaigns.component';
import { PASTCAMPAIGNSComponent } from './pastcampaigns/pastcampaigns.component';


const routes: Routes = [
  {
    path:'upcomingcampaigns',
    component: UPCOMINGCAMPAIGNSComponent
  },
  {
    path:'livecampaigns',
    component: LIVECAMPAIGNSComponent
  },
  {
    path:'pastcampaigns',
    component: PASTCAMPAIGNSComponent
  }
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents =[UPCOMINGCAMPAIGNSComponent,LIVECAMPAIGNSComponent,PASTCAMPAIGNSComponent ]